<template>
    <div icon="autorenew" class="system-content">
        <div class="router-view-content" :style="vIF && false ? 'height: 95%': 'height: 100%' " ><router-view></router-view></div>
  	</div>
</template>

<script>
export default {
    name: 'SystemContent',
    data() {
        return {
            aba_atual: {},
            showPesquisa: false,
        }
    },
    computed: {
        getAbas(){
            return this.$store.getters['abas.getAbas'][this.$route.path]
        },
        getPath(){
            return this.$route.path
        },
        vIF(){
            return this.getAbas != null && this.getAbas != undefined && this.getAbas.length > 0
        }
    },
    methods: {
		select_aba(index){
            if (this.vIF){
                this.aba_atual = this.getAbas[index]
                this.$store.dispatch('abas.set', [this.$route.path, index])
                this.$store.dispatch('abas.setFiltro', [this.$route.path, {page: 1}])
            }
		}
    },
    watch: {
        getPath(){
            const index = this.$store.getters['abas.getIndices'][this.$route.path]
            this.select_aba(index != undefined ? index : 0) 
        },
    },
    mounted() {
        const index = this.$store.getters['abas.getIndices'][this.$route.path]
        this.select_aba(index != undefined ? index : 0)
    },
}
</script>

<style scoped>
.system-content{
    grid-area: system-content;
    background-color: var(--cinza);
    overflow-y: auto;
    padding: 10px;
    padding-top: 0;
}
.router-view-content{
    height: 95%;
}
.titulo{
    height: 5%;
	background-color: var(--c1);
	display: flex;
	justify-content: space-between;
	align-items: stretch;
}
.nomes {
	display: flex;
	flex-wrap: wrap;
}
.filtro {
	display: flex;
    align-items: center;
}
.nomes-card {
	padding: 10px 25px;
	color: var(--branco);
	display: flex;
	justify-content: center;
	align-items: center;
    /*border-radius: 0px 15px 0px 0px;*/
    height: 100%;
    font-size: 0.9rem;
}
.nomes-card:hover {
	cursor: pointer;
	opacity: 0.8;
}
#background1{
    background-color: var(--c4);
    font-style: italic;
}
#background2{
	background-color: var(--c1);
}
@media only screen and (max-width: 800px) {
	.titulo, .nomes{
        /*flex-wrap: wrap;*/
        flex-direction: column;
        justify-content: left;
		font-size: 1em;
	}
	.nomes-card {
		padding: 8px 8px;
       
	}
    .filtro{
        display: none;
    }

}
</style>